// app/components/MovieCarousel.tsx
import { Movie } from '@/types';
import Image from 'next/image';

interface MovieCarouselProps {
  movies: Movie[];
  onSelectMovie: (movie: Movie) => void;
  selectedMovie: Movie | null;
}

export default function MovieCarousel({ movies, onSelectMovie, selectedMovie }: MovieCarouselProps) {
  return (
    <div className="overflow-x-auto pb-6">
      <div className="flex gap-4 min-w-full">
        {movies.map((movie) => (
          <div 
            key={movie.id}
            onClick={() => onSelectMovie(movie)}
            className="flex-none cursor-pointer group"
          >
            <div className="relative w-[180px] aspect-[2/3] rounded-lg overflow-hidden">
              <Image
                src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                alt={movie.title}
                fill
                sizes="180px"
                className={`object-cover transition-transform duration-300 group-hover:scale-105 ${
                  selectedMovie?.id === movie.id ? 'ring-2 ring-yellow-400' : ''
                }`}
              />
              <div className="absolute inset-0 bg-gradient-to-t from-black to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300">
                <div className="absolute bottom-0 p-4 w-full">
                  <h3 className="text-white text-sm font-semibold line-clamp-2">
                    {movie.title}
                  </h3>
                  <div className="flex items-center gap-2 mt-1">
                    <span className="text-yellow-400 text-xs">
                      {movie.vote_average.toFixed(1)}
                    </span>
                    <span className="text-gray-300 text-xs">
                      {new Date(movie.release_date).getFullYear()}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}